import React, { useEffect, useState } from "react";
import styled from "styled-components";

import { parser } from "@matt-dunn/parse-example";

import { JSONEditor } from "./JSONEditor";

const TextAreaContainer = styled.div`
    display: grid;
    height: 100%;

    &::after {
        content: attr(data-replicated-value) " ";
        white-space: pre-wrap;
        visibility: hidden;
    }
    & > textarea {
        resize: none;
        overflow: hidden;
    }
    & > textarea,
    &::after {
        font: inherit;
        grid-area: 1 / 1 / 2 / 2;
        word-break: break-word;
    }
`;

const Container = styled.div`
    flex-grow: 1;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-template-areas:
        "value value"
        "metadata metadata"
        "ast transformed-ast";
    grid-template-rows: minmax(15em, calc(30% - 1em)) minmax(10em, calc(10% - 1em)) minmax(
            10em,
            60%
        );
    gap: 1em;
    overflow: auto;

    pre {
        white-space: pre-wrap;

        &.ast {
            font-size: 80%;
        }
    }

    .flex {
        display: flex;
        flex-grow: 1;
        flex-direction: column;
    }

    .value {
        white-space: pre-wrap;
        word-break: break-word;

        .type {
            margin-bottom: 0.5em;
            color: #888;
        }
    }

    .scrollable {
        overflow: auto;
    }

    .main {
        display: grid;
        gap: 1em;
        grid-template-columns: repeat(2, minmax(0, 1fr));

        &.outline {
            > :last-child {
                border-left: 1px solid #555;
            }
        }
    }

    .outline {
        border: 1px solid #555;
    }

    header {
        margin-bottom: 0.25em;
        font-weight: bold;
    }
`;

// Test data - @TODO: add to UI
const context = {};
const basePath = undefined;

type TokenizerProps = {
    className?: string;
};

export const Tokenizer = ({ className }: TokenizerProps) => {
    const [data, setData] = useState({
        a: 123456,
        b: {
            a: 'xx"xA',
            b: 654321
        },
        c: [34, "a1", true, "a2", 123456],
        url: "value = 'xxx'",
        url2: "www.test.com?title=Hello World",
        url3: `www.test.com?title=Hello World
                
        www.test.com?title=Hello World`,
        booleanTrueProp: true,
        booleanFalseProp: false,
        prop: "test",
        test1: true,
        test2: false,
        u: undefined,
        n: null,
        values: [{ a: 34 }, { a: 42 }, { a: 69 }, { a: 3 }]
    });

    const [value, setValue] = useState(
        `@number(@calc(34 + @ref( b.b   , 10.5)), {"currency":"gbp", "style":"currency"})`
    );

    const [error, setError] = useState<Error | undefined>();
    const [result, setResult] = useState<ReturnType<typeof parser>>();

    useEffect(() => {
        try {
            setError(undefined);
            setResult(parser(value, data, context, basePath));
        } catch (ex) {
            setError(ex as Error);
            setResult(undefined);
        }
    }, [data, value]);

    return (
        <Container className={className}>
            <section
                className="flex section section__value"
                style={{ gridArea: "metadata" }}
            >
                <header>
                    Dataset{" "}
                    <sub>
                        (<code>@ref</code> paths)
                    </sub>
                </header>
                <JSONEditor
                    className="outline"
                    defaultValue={data}
                    onChange={setData}
                />
            </section>
            <section
                className="flex section section__value"
                style={{ gridArea: "value" }}
            >
                <section className="main">
                    <header>Value</header>
                    <header>Transformed Value</header>
                </section>

                <section className="main flex scrollable outline">
                    <article className="flex">
                        <section className="flex">
                            <TextAreaContainer data-replicated-value={value}>
                                <textarea
                                    className="flex"
                                    value={value}
                                    onChange={e => setValue(e.target.value)}
                                />
                            </TextAreaContainer>
                        </section>
                    </article>

                    <article className="flex">
                        <section className="flex">
                            <div className="value">
                                {typeof result?.value !== "string" ? (
                                    <>
                                        <div className="type">
                                            Type:{" "}
                                            <strong>
                                                {Array.isArray(result?.value)
                                                    ? "array"
                                                    : typeof result?.value}
                                            </strong>
                                        </div>
                                        {JSON.stringify(result?.value, null, 2)}
                                    </>
                                ) : (
                                    result?.value?.toString()
                                )}
                            </div>
                        </section>
                    </article>
                </section>
                {error && <pre className="error">{error.message}</pre>}
            </section>
            <section
                className="flex section section__ast"
                style={{ gridArea: "ast" }}
            >
                <header>Value AST</header>
                <section className="flex scrollable outline">
                    {result?.tokens && (
                        <pre className="ast">{JSON.stringify(result.tokens, null, 2)}</pre>
                    )}
                </section>
            </section>
            <section
                className="flex section section__ast"
                style={{ gridArea: "transformed-ast" }}
            >
                <header>Transformed AST</header>
                <section className="flex scrollable outline">
                    {result?.transformedTokens && (
                        <pre className="ast">
                            {JSON.stringify(
                                result.transformedTokens,
                                (name, value) => (name === "parent" ? undefined : value),
                                2
                            )}
                        </pre>
                    )}
                </section>
            </section>
        </Container>
    );
};
