import { resolve } from "path";

import type { DotPath } from "./dotPathToPath";

const pathPrefix = (path: DotPath | undefined) => {
    if (path && typeof path === "string") {
        if (path.startsWith("..")) {
            return `<P>${path.substring(2)}`;
        }
        if (path.startsWith(".")) {
            return `<R>${path.substring(1)}`;
        }
        return `<A>${path}`;
    }

    return undefined;
};

const normalizeDotPath = (path: DotPath | undefined) =>
    pathPrefix(path)
        ?.replace(/\[.*?\]/g, val => val.replace(/\./g, "<AKD>").replace(/\//g, "<AKS>"))
        ?.replace(/\.\./g, "<PI>")
        ?.replace(/\./g, "/")
        ?.replace(/<PI>/g, "/../")
        ?.replace(/<P>/g, "../")
        ?.replace(/<R>/g, "./")
        ?.replace(/<A>/g, "/");

const denormalizeDotPath = (path: DotPath) =>
    path?.replace(/\[.*?\]/g, val => val.replace(/<AKD>/g, ".").replace(/<AKS>/g, "/"));

export const resolveDotPath = (path: DotPath, basePath?: DotPath) =>
    denormalizeDotPath(
        resolve("/", normalizeDotPath(basePath) || "", normalizeDotPath(path) || "")
            ?.substring(1)
            .replace(/\//g, ".")
    );
