import type { Token } from "./types";

export const trimStart = <T extends Token>(tokens?: T[]) => {
    if (tokens && tokens?.length > 0) {
        const index = tokens.findIndex(({ type }) => type !== "whitespace");
        if (index !== -1) {
            return tokens.slice(index);
        }
    }

    return tokens;
};

export const trimEnd = <T extends Token>(tokens?: T[]) => {
    if (tokens && tokens?.length > 0) {
        const index = tokens.findLastIndex(({ type }) => type !== "whitespace");
        if (index !== -1) {
            return tokens.slice(0, index + 1);
        }
    }

    return tokens;
};

export const trim = <T extends Token>(tokens?: T[]) => trimStart(trimEnd(tokens));

export const truncateWithOffsetMarker = (
    value: string,
    offset: number,
    offsetStart = 20,
    offsetEnd = 20,
    truncateMarker = "…"
) => {
    if (typeof value === "string") {
        const normalizedValue = value.replace(/\n/g, " ");
        const truncatedValue = normalizedValue.slice(
            Math.max(offset - offsetStart, 0),
            offset + offsetEnd
        );
        const offsetIndex = offset - Math.max(offset - offsetStart, 0);
        const truncateMarkerStart = offset >= offsetStart ? truncateMarker : "";
        const truncateMarkerEnd = normalizedValue.length > offset + offsetEnd ? truncateMarker : "";

        return `${truncateMarkerStart}${truncatedValue}${truncateMarkerEnd}\n${" ".repeat(
            offsetIndex + truncateMarkerStart.length
        )}^`;
    }

    return value;
};
