import {
    create,
    fractionDependencies,
    addDependencies,
    divideDependencies,
    formatDependencies,
    evaluateDependencies,
    andDependencies,
    orDependencies,
    notDependencies,
    isDenseMatrix,
    type Matrix
} from "mathjs";

const math = create({
    fractionDependencies,
    addDependencies,
    divideDependencies,
    formatDependencies,
    andDependencies,
    orDependencies,
    notDependencies,
    evaluateDependencies
});

math.import({
    encodeURIComponent,
    decodeURIComponent,
    encodeURI,
    decodeURI,
    array: (...args: (Matrix | unknown)[]) =>
        args?.flatMap(i => (isDenseMatrix(i) ? i.toArray() : i)),
    total: (data: Matrix | Record<string, number>[], propName: string) => {
        const dataset = isDenseMatrix(data)
            ? (data?.toArray?.() as unknown as Record<string, number>[])
            : data;
        if (!Array.isArray(dataset)) {
            throw new TypeError(`Dataset must be an array, got ${typeof dataset}`);
        }
        return dataset.reduce((total, value) => total + (value?.[propName] ?? 0), 0);
    }
});

export { math };
