import { coerce } from "@matt-dunn/utils";

import { join } from "./join";
import { TokenSyntaxError } from "./TokenSyntaxError";
import type { Context, Token, TokenType } from "./types";

const valueBrackets = {
    "[": "]",
    "{": "}"
};
const brackets = {
    "(": ")",
    ...valueBrackets
};
const quotes = "`'\"";
const functionPrefix = "@";
const parameterSeparator = ",";

const reSpace = /[\s]/;
const reSpaceEnd = /[^\s]/;
const reBracketsOpen = new RegExp(`[${Object.keys(brackets).join("")}]`);
const reFunction = new RegExp(`^${functionPrefix}(?<name>[a-z][a-z0-9_]*?)\\(`, "i");

const allBrackets = Object.entries(brackets)
    .map(([open, close]) => `${open}${close}`)
    .join("");

const ESCAPE_WHITELIST = `${quotes}${functionPrefix}${allBrackets}${parameterSeparator}`;

export const tokenize = (
    value: string,
    context: Context = { cursor: 0, parent: { type: "root" } }
) => {
    const tokens: Token[] = [];

    let cursor = 0;
    let activeToken: Token | undefined;

    while (cursor < value.length) {
        const current = value[cursor];
        const restValue = value.substring(cursor);
        let consumed = false;

        try {
            // Not escaped...
            if (value[cursor - 1] !== "\\") {
                let currentToken: Token | undefined;
                let skip = false;

                // Grammer...
                if (context.parent.type === "brackets") {
                    const children = tokenize(restValue, {
                        cursor: cursor + context.cursor,
                        parent: { type: "parameter" }
                    });
                    currentToken = {
                        type: "parameter",
                        value: undefined,
                        tokens: children.tokens,
                        cursor: cursor + context.cursor
                    };

                    cursor += children.cursor + 1;
                } else if (
                    context.parent.type === "parameter" &&
                    parameterSeparator.indexOf(current) !== -1
                ) {
                    break; // Defer back to parent
                } else if (functionPrefix.indexOf(current) !== -1) {
                    const functionMatch = restValue.match(reFunction);
                    if (functionMatch?.groups?.name) {
                        const consume = functionMatch.groups.name.length + 1;

                        const children = tokenize(restValue.substring(consume), {
                            cursor: cursor + context.cursor + consume,
                            parent: { type: "function" }
                        });
                        currentToken = {
                            type: "function",
                            value: functionMatch.groups.name,
                            tokens: children.tokens,
                            cursor: cursor + context.cursor
                        };

                        cursor += consume + children.cursor;
                    } else {
                        throw new TokenSyntaxError(
                            "Invalid function syntax",
                            cursor + context.cursor
                        );
                    }
                } else if (
                    (context.parent.type === "function" || context.parent.type === "parameter") &&
                    reBracketsOpen.test(current)
                ) {
                    let bracketCount = 1;
                    let consume = 1;

                    const bracketOpen = current as keyof typeof brackets;
                    const bracketClose = brackets[bracketOpen];

                    while (consume < restValue.length && bracketCount !== 0) {
                        if (restValue[consume] === bracketOpen && restValue[consume - 1] !== "\\") {
                            bracketCount += 1;
                        }
                        if (
                            restValue[consume] === bracketClose &&
                            restValue[consume - 1] !== "\\"
                        ) {
                            bracketCount -= 1;
                        }
                        consume += 1;
                    }

                    if (bracketCount > 0) {
                        throw new TokenSyntaxError(
                            `Expected closing bracket: ${bracketClose}`,
                            cursor + context.cursor
                        );
                    }

                    const type: TokenType = valueBrackets[current as keyof typeof valueBrackets]
                        ? "value"
                        : "brackets";
                    const children = tokenize(restValue.substring(1, consume - 1), {
                        cursor: cursor + context.cursor + 1,
                        parent: { type }
                    });
                    currentToken = {
                        type,
                        value:
                            type === "value"
                                ? coerce(`${bracketOpen}${join(children.tokens)}${bracketClose}`)
                                : `${bracketOpen}${bracketClose}`,
                        tokens: type === "value" ? undefined : children.tokens,
                        cursor: cursor + context.cursor
                    } as Token;

                    cursor += consume;

                    skip = true;
                } else if (quotes.indexOf(current) !== -1) {
                    const nextIndex = new RegExp(`[^\\\\]${current}`).exec(restValue)?.index;
                    const consume = typeof nextIndex === "number" ? nextIndex + 1 : undefined;

                    if (consume === undefined && context.parent.type !== "quote") {
                        throw new TokenSyntaxError(
                            `Expected closing quote: ${current}`,
                            cursor + context.cursor
                        );
                    }

                    if (consume !== undefined) {
                        const children = tokenize(restValue.substring(1, consume), {
                            cursor: cursor + context.cursor + 1,
                            parent: { type: "quote" }
                        });
                        currentToken = {
                            type: "quote",
                            value: current,
                            tokens: children.tokens,
                            cursor: cursor + context.cursor
                        };

                        cursor += consume + 1;
                    }
                } else if (context.parent.type !== "quote" && reSpace.exec(current) !== null) {
                    const nextIndex = reSpaceEnd.exec(restValue)?.index;
                    const consume = typeof nextIndex === "number" ? nextIndex : value.length;

                    currentToken = {
                        type: "whitespace",
                        value: restValue.substring(0, consume),
                        cursor: cursor + context.cursor
                    };

                    cursor += consume;
                }

                if (currentToken) {
                    if (activeToken) {
                        tokens.push(activeToken);
                        activeToken = undefined;
                    }
                    tokens.push(currentToken);
                    consumed = true;
                }

                if (skip) {
                    break;
                }
            }

            if (!consumed) {
                if (current === "\\" && ESCAPE_WHITELIST.indexOf(value[cursor + 1]) !== -1) {
                    cursor += 1;
                } else {
                    if (!activeToken || activeToken.type !== "literal") {
                        if (activeToken) {
                            tokens.push(activeToken);
                        }
                        activeToken = {
                            type: "literal",
                            value: "",
                            cursor: cursor + context.cursor
                        };
                    }

                    activeToken.value += current;

                    cursor += 1;
                }
            }
        } catch (ex) {
            if (ex instanceof Error) {
                throw new TokenSyntaxError(ex.message, cursor);
            }

            throw ex;
        }
    }

    if (activeToken) {
        tokens.push(activeToken);
    }

    return {
        tokens,
        cursor
    };
};
