import { coerce } from "@matt-dunn/utils";

import { TokenSyntaxError } from "./TokenSyntaxError";
import { join } from "./join";
import type { TransformToken } from "./types";
import { trim } from "./utils";

export type TransformMetadata = {
    name: string | undefined;
    args?: unknown[] | undefined;
    tokens?: TransformToken[];
};

export const transformMetadata = (token: TransformToken): undefined | TransformMetadata => {
    if (token.type === "function") {
        const brackets = token.tokens?.[0]?.type === "brackets" && token.tokens[0];

        if (brackets) {
            return {
                name: token.value,
                tokens: brackets.tokens,
                args: brackets.tokens
                    ?.filter(({ type }) => type === "parameter")
                    ?.reduce((parameters, parameter) => {
                        const normalizedTokens = trim(parameter?.tokens);

                        try {
                            if (
                                normalizedTokens?.length === 1 &&
                                normalizedTokens[0].type === "value"
                            ) {
                                return [...parameters, normalizedTokens[0].value];
                            }

                            if (
                                normalizedTokens?.length === 1 &&
                                normalizedTokens[0].type === "quote"
                            ) {
                                return [...parameters, join(normalizedTokens[0].tokens, "quote")];
                            }

                            return [...parameters, coerce(join(normalizedTokens, "literal"))];
                        } catch (ex) {
                            if (ex instanceof SyntaxError) {
                                throw new TokenSyntaxError(ex.message, parameter.cursor);
                            }
                            throw ex;
                        }
                    }, [] as unknown[])
            };
        }
    }

    return undefined;
};
