export class TokenSyntaxError extends SyntaxError {
    static type = "TokenSyntaxError";

    public cursor?: number;

    constructor(message: string, cursor?: number) {
        super(`${message}${typeof cursor === "number" ? ` at position ${cursor}` : ""}`);
        this.name = TokenSyntaxError.type;
        this.cursor = cursor;
    }
}
