import type { Path } from "./pathToDotPath";

export type DotPath = string;

export const dotPathToPath = (path: DotPath): Path =>
    (path !== "" &&
        path
            ?.replace(/\[.*?\]/g, val => val.replace(/\./g, "<AKD>").replace(/\//g, "<AKS>"))
            ?.split(".")
            ?.map(part => part.replace(/<AKD>/g, ".").replace(/<AKS>/g, "/"))) ||
    [];
