export const getIn = (o: unknown, path?: (string | number)[], defaultValue?: unknown) => {
    if (typeof o === "object" && path) {
        return path?.length > 0
            ? path.reduce(
                  (value, part) => {
                      if (value && Object.prototype.hasOwnProperty.call(value, part)) {
                          return value[part] as Record<string | number, unknown>;
                      }
                      return defaultValue as Record<string | number, unknown>;
                  },
                  o as Record<string | number, unknown>
              )
            : o;
    }

    return o ?? defaultValue;
};
