import { dotPathToPath, type DotPath, resolveDotPath, getIn } from "@matt-dunn/utils";
import { transform, type Factory, Tokenizer } from "@matt-dunn/tokenizer";

import { math } from "./math";

const functionFactory: Factory = {
    encodeURIComponent: {
        eval: () => (value: unknown) => value && encodeURIComponent(JSON.stringify(value))
    },
    encodeURI: {
        eval: () => (value: unknown) => value && encodeURI(JSON.stringify(value))
    },
    string: {
        eval: () => (value: unknown) => value?.toString()
    },
    number: {
        eval: () => (value: unknown, options: Intl.NumberFormatOptions) => {
            if (typeof value === "number") {
                return value.toLocaleString(undefined, options);
            }

            return value;
        }
    },
    ref: {
        eval:
            ({ data, basePath }) =>
            (path: DotPath, defaultValue: unknown) => {
                const value = getIn(data, dotPathToPath(resolveDotPath(path, basePath)));
                return value ?? defaultValue ?? value;
            }
    },
    context: {
        eval:
            ({ context, basePath }) =>
            (path: DotPath, defaultValue: unknown) => {
                const value = getIn(context, dotPathToPath(resolveDotPath(path, basePath)));
                return value ?? defaultValue ?? value;
            }
    },
    calc: {
        transform: token => {
            const scope = {} as Record<string, unknown>;

            return {
                token: {
                    ...token,
                    tokens: transform(
                        token.tokens,
                        (visitedToken, id) => {
                            if (
                                visitedToken.type === "value" &&
                                visitedToken?.parent?.type !== "quote"
                            ) {
                                scope[id] = visitedToken.value;

                                return {
                                    ...visitedToken,
                                    type: "literal" as const,
                                    value: id
                                };
                            }

                            return visitedToken;
                        },
                        token
                    )
                },
                scope: { context: scope }
            };
        },
        eval:
            ({ context }) =>
            (expression: string) => {
                const value =
                    (typeof expression === "string" && expression?.replace(/[\s]+/g, " ")) ||
                    undefined;

                try {
                    return value !== undefined ? math.evaluate(value, context) : undefined;
                } catch (ex) {
                    throw new SyntaxError(
                        `mathjs: ${(ex as Error)?.message} (Expression: '${value}')`
                    );
                }
            }
    }
};

const { parser } = Tokenizer({
    functionFactory
});

export { parser };
