import { type Path, pathToDotPath } from "./pathToDotPath";
import { type DotPath, dotPathToPath } from "./dotPathToPath";
import { parseIndexPathPart } from "./parseIndexPathPart";
import { getIn } from "./getIn";

const NO_VALUE = "NO_VALUE";

export const resolveArrayPath = <P extends Path | DotPath, R = P extends Path ? Path : DotPath>(
    path: P,
    o: unknown
): R | undefined => {
    const pathParts: Path = Array.isArray(path) ? path : dotPathToPath(path);

    const resolvedPathParts = pathParts?.reduce(
        (resolvedPath, part, i) => {
            const indexPart = parseIndexPathPart(part);
            if (indexPart) {
                const [propName, propValue] = indexPart;
                const value =
                    (o && resolvedPath && getIn(o, resolvedPath.slice(0, i), NO_VALUE)) ||
                    undefined;
                if (value !== NO_VALUE) {
                    if (Array.isArray(value)) {
                        const index = value?.findIndex(
                            current => current[propName]?.toString() === propValue?.toString()
                        );
                        if (index === -1) {
                            return undefined;
                        }
                        return resolvedPath ? [...resolvedPath, index] : [index];
                    }
                    if (value !== undefined && value !== null) {
                        console.warn(
                            `Invalid path '${pathToDotPath(
                                pathParts
                            )}' - '${part}' does not resolve to an array, found ${typeof value}.${
                                o === undefined || o === null
                                    ? ` In addition, the source object is undefined.`
                                    : ""
                            }`
                        );
                    }
                }
                return undefined;
            }

            return resolvedPath && [...resolvedPath, part];
        },
        [] as Path | undefined
    );

    return (Array.isArray(path) ? resolvedPathParts : pathToDotPath(resolvedPathParts)) as R;
};
