import { coerce } from "@matt-dunn/utils";

import { TokenSyntaxError } from "./TokenSyntaxError";
import type { Token, TokenType } from "./types";
import { trim } from "./utils";

const serialzeValue = (value: unknown): string | undefined => {
    if (Array.isArray(value)) {
        return `[${value.map(serialzeValue).join(",")}]`;
    }

    if (typeof value === "object") {
        return JSON.stringify(value);
    }

    return value?.toString();
};

export const join = (tokens: Token[] | undefined, parent: TokenType = "root"): unknown => {
    const normalizedTokens = trim(tokens);
    if (
        normalizedTokens?.length === 1 &&
        (normalizedTokens[0].type === "literal" || normalizedTokens[0].type === "value")
    ) {
        if (typeof normalizedTokens?.[0]?.value === "string" && parent !== "quote") {
            try {
                return coerce(normalizedTokens[0].value);
            } catch (ex) {
                if (ex instanceof SyntaxError) {
                    throw new TokenSyntaxError(ex.message, normalizedTokens[0].cursor);
                }
                throw ex;
            }
        }
        return normalizedTokens[0].value;
    }

    return tokens
        ?.map(token => {
            const children = serialzeValue((token.tokens && join(token.tokens, token.type)) ?? "");

            if (token.type === "brackets") {
                return `${token.value?.[0] || "("}${children}${token?.value?.[1] || ")"}`;
            }
            if (token.type === "quote") {
                return `${token.value ?? '"'}${children?.replaceAll(
                    token.value ?? '"',
                    `\\${token.value ?? '"'}`
                )}${token.value}`;
            }
            if (token.type === "function") {
                return `@${token.value ?? ""}${children}`;
            }
            if (token.type === "value") {
                return `${serialzeValue(token.value) ?? ""}${children}`;
            }
            return `${token.value ?? ""}${children}`;
        })
        .join(parent === "brackets" ? "," : "");
};
