import type { Token, TransformToken } from "./types";

export type Transform = (token: TransformToken, id: string) => Token | undefined;

export const transform = (
    tokens: TransformToken[] | undefined,
    visit: Transform,
    parent: TransformToken = { type: "root", value: undefined, cursor: 0 }
): TransformToken[] | undefined =>
    tokens?.map((visitToken, index) => {
        const id = `${parent.id ?? "id"}_${index}`;
        const children =
            visitToken.tokens &&
            transform(visitToken.tokens, visit, {
                ...visitToken,
                parent,
                index,
                id
            });

        const transformedToken = {
            ...visitToken,
            parent,
            tokens: children
        } as TransformToken;

        const token = visit(transformedToken, id) || transformedToken;

        return {
            ...token,
            tokens: token.tokens
        } as TransformToken;
    });
