import { type DotPath } from "@matt-dunn/utils";

export type TokenType =
    | "root"
    | "brackets"
    | "parameter"
    | "function"
    | "quote"
    | "literal"
    | "whitespace"
    | "value";

export type Context = {
    cursor: number;
    parent: {
        type: TokenType;
    };
};

type TokenBase<T extends TokenType, V = string> = {
    type: T;
    value: V | undefined;
    tokens?: Token[];
    cursor: number;
};

export type Token =
    | TokenBase<"value", unknown>
    | TokenBase<"parameter", never>
    | TokenBase<"brackets" | "function" | "literal" | "quote" | "root" | "whitespace", string>;

type TransformTokenBase<T extends TokenType, V = string> = {
    type: T;
    value: V | undefined;
    tokens?: TransformToken[];
    cursor: number;
    parent?: TransformToken;
    id?: string;
    index?: number;
};

export type TransformToken =
    | TransformTokenBase<"value", unknown>
    | TransformTokenBase<"parameter", never>
    | TransformTokenBase<
          "brackets" | "function" | "literal" | "quote" | "root" | "whitespace",
          string
      >;

export type Scope = {
    data?: Record<string, unknown>;
    context?: Record<string, unknown>;
    basePath?: DotPath;
};

type FactoryFunction = (scope: Scope) => (...args: any[]) => unknown;

export type Factory = {
    [name: string]: {
        eval: FactoryFunction;
        transform?: (
            token: TransformToken,
            scope: Scope
        ) => { token: TransformToken; scope?: Scope };
    };
};
