import { isNumber } from "./isNumber";

type CoerceResult<T> = T extends string
    ? number | boolean | null | undefined | string | Record<string, unknown> | Array<unknown>
    : T;

export const coerce = <V extends string | unknown, R extends CoerceResult<V>>(value: V): R => {
    if (typeof value === "string") {
        const normalizedValue = value.trim().toLowerCase();

        if (isNumber(normalizedValue)) {
            return parseFloat(normalizedValue) as R;
        }
        if (normalizedValue === "true") {
            return true as R;
        }
        if (normalizedValue === "false") {
            return false as R;
        }
        if (normalizedValue === "null") {
            return null as R;
        }
        if (normalizedValue === "undefined") {
            return undefined as R;
        }
        if (normalizedValue?.startsWith("[") || normalizedValue?.startsWith("{")) {
            return JSON.parse(value) as R;
        }
    }

    return value as unknown as R;
};
