import { type DotPath, coerce, resolveArrayPath } from "@matt-dunn/utils";

import { truncateWithOffsetMarker } from "./utils";
import { TokenSyntaxError } from "./TokenSyntaxError";
import { join } from "./join";
import { tokenize } from "./tokenize";
import { transform } from "./transform";
import { transformMetadata } from "./transformMetadata";
import type { Factory, Scope, Token, TransformToken } from "./types";

type TokenizerOptions = {
    functionFactory: Factory;
};

export const Tokenizer = ({ functionFactory }: TokenizerOptions) => {
    const parser = <R = unknown>(
        value: string,
        data?: Record<string, unknown>,
        context?: Record<string, unknown>,
        basePath?: DotPath
    ): {
        value: R;
        tokens?: Token[];
        transformedTokens?: TransformToken[];
    } => {
        if (typeof value !== "string") {
            return value;
        }
        const resolvedBasePath = resolveArrayPath(basePath || "", data);

        const scope: Scope = { data, context, basePath: resolvedBasePath };

        try {
            const ast = tokenize(value);

            const transformedAst = transform(ast.tokens, token => {
                if (token.type === "function") {
                    const func = functionFactory[token.value || ""];

                    if (!func) {
                        throw new TokenSyntaxError(
                            `Function '${token.value}' not defined`,
                            token.cursor
                        );
                    }

                    const transformed = func?.transform?.(token, scope);
                    const metadata = transformMetadata(transformed?.token || token);

                    if (metadata) {
                        try {
                            return {
                                ...token,
                                type: "value",
                                value: func
                                    ?.eval?.({ ...scope, ...transformed?.scope })
                                    .call(null, ...(metadata.args || [])),
                                tokens: undefined
                            };
                        } catch (ex) {
                            throw new TokenSyntaxError(
                                `(${(ex as Error)?.name || "unknown"}) ${
                                    (ex as Error)?.message || "Unknown error"
                                }`,
                                token.cursor
                            );
                        }
                    }
                }

                return undefined;
            });

            return {
                value: (function iife() {
                    const result = join(transformedAst);
                    try {
                        return coerce(result) as R;
                    } catch {
                        // Ignore
                        return result as R;
                    }
                })(),
                tokens: ast.tokens,
                transformedTokens: transformedAst
            };
        } catch (ex) {
            if (ex instanceof TokenSyntaxError) {
                throw new SyntaxError(
                    `${ex.message}\n\n${truncateWithOffsetMarker(value, ex.cursor || 0)}`
                );
            }
            throw ex;
        }
    };

    return { parser };
};
